.nav {
  outline: 1px solid rebeccapurple;
  width: 100%;
  padding: 8px 0;
  margin: auto 0 0 0;
  display: inline-flex;
  justify-content: space-around;

  &__item {
    text-decoration: none;
    color: #c6c6c6;
    display: flex;
    flex-flow: column;
    align-items: center;
    justify-items: flex-end;
    flex: 1 1 0;
    gap: .5rem;
    font-size: 12px;
  }
}
